.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0;
  overflow: hidden;
  height: 100%;
  padding: 0 .5rem;
  position: fixed;
  z-index: 10;
  left: 0;
}

h1 {
  color: #000000;
  font-family: 'buffon', serif;
  font-size: 3.8rem;
  font-weight: 300;
  margin: 0;
  padding: 1.5rem 0;
  text-orientation: mixed;
  transform: rotate(180deg);
  writing-mode: vertical-rl;
  text-transform: lowercase;
}

.download-button {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.download-arrow {
  width: 2.5rem;
}

.download-text{
  margin: 1.5rem 0;
}
.download-list {
  margin-bottom: -7.5rem;
  transition: margin-bottom 1s ease;
}
.download-button.visible .download-list {
  margin-bottom: 2rem;
}
.download-list li {
  margin: 1rem 0;
}

@media (max-width: 50rem) {
  .App-header {
    position: absolute;
    padding: 0.5rem;
    z-index: 10;
  }
  h1 {
    display:block;
    visibility:hidden;
    transform: none;
    writing-mode: initial;
    padding: 0 .5rem;
    font-size: 3rem;
  }
  h1::first-letter {
    visibility: visible;
  }
  .download-text, .download-list{
    display: none;
  }
  .download-button {
    align-self: flex-start;
    margin: .8rem .5rem;
  }
}
